/**
* @Author: duxin
* description: 首页
*/

<template>
  <div class="index-wrap">
    <div class="index-header-wrap">
      <HomeButton class="index-button" icon="icon-shouye" name="首页" :active="isActiveIndex===0"
                  @click="onNavClick(0)"></HomeButton>
      <HomeButton class="index-button" icon="icon-guanyuwomen" name="关于" :active="isActiveIndex===1"
                  @click="onNavClick(1)"></HomeButton>
    </div>
    <div class="index-saying">
      <h1 class="index-saying-h1">秋过冬漫长</h1>
      <p class="index-saying-h5">来来往往总是那么几个人</p>
    </div>
    <div class="index-content">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import HomeButton from "../components/blog-home-button"

  export default {
    components: {
      HomeButton
    },
    data() {
      return {
        isActiveIndex: 0
      }
    },
    methods: {
      onNavClick(index) {
        this.isActiveIndex = index;
        switch (index) {
          case 0:
            this.$router.push("/");
            break;
          case 1:
            this.$router.push("about");
            break;
        }
      }
    }
  }
</script>

<style scoped>
  .index-wrap {
    height: 100%;
    width: 1100px;
    margin: 0 auto;
  }

  .index-header-wrap {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 20px 0 0 0;
    color: white;
  }

  .index-button {
    margin-right: 15px;
  }

  .index-saying {
    margin-top: 30px;
  }

  .index-saying-h1 {
    font: bold 42px/1.2 "宋体";
    white-space: nowrap;
    color: #fff;
    text-shadow: 1px 3px 6px #113f6e;
    margin: 0
  }

  .index-saying-h5 {
    color: #fff;
    text-shadow: 1px 2px 3px #113f6e;
    white-space: normal;
    word-wrap: break-word;
    line-height: 26px;
  }

  .index-content {
    margin-top: 130px;
    margin-bottom: 20px;
  }

</style>



