/**
* @Author: duxin
* description: 首页顶部导航按钮
*/

<template>
  <div :class="{ 'blog-home-button':true,'active':this.active}" @click="onClick">
    <i :class="iconClazz"></i>
    <span>{{name}}</span>
  </div>
</template>

<script>
  export default {
    name: "blog-home-button",
    props: {
      name: String,
      icon: String,
      active: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        iconClazz: ["iconfont", this.icon],
      }
    },
    methods: {
      onClick() {
        this.$emit("click")
      }
    }
  }
</script>

<style scoped>
  .blog-home-button {
    cursor: pointer;
    padding: 10px 15px;
  }

  .active {
    border-bottom: 1px solid #fff;
    box-shadow: 0 0 10px #adc2d7 inset;
  }

  .iconfont {
    font-size: 18px;
  }
</style>



